import React from 'react';
import styles from './styles/MagazineItem.module.scss';
import BlueMagazineData from '../assets/data/BlueMagazineData';
import { MagazineItem } from './MagazineItem';
import { BrowserRouter as Router } from 'react-router-dom'; // 수정된 부분

export const MagazineList = () => {
    return (
        <Router>
            <ul className={styles['blue-magazine-list']}>
                {BlueMagazineData.map(({ listLink, image, imageText,name, date, title, text }, index) => (
                <MagazineItem
                     key={index}
                     listLink={listLink}
                     image={image}
                     imageText={imageText}
                     name={name}
                     date={date}
                     title={title}
                     text={text}
                 />
                ))}
            </ul>
        </Router>
    );
};